import Select from 'react-select';
import React from 'react';
import i18n from '../i18n/i18n';
import MenuItem from '@mui/material/MenuItem';

const customStyles = {
  control: (provided, state) => ({
    ...provided,
   backgroundColor: 'black',
    width: '100px', // Rduire la largeur du contrle
    minHeight: '30px', // Rduire la hauteur du contrle
    border: 'none',
  }),
  menuList: (provided, state) => ({
    ...provided,
    '& div': {
      '&:hover': {
        backgroundColor: 'black', // Couleur du hover sur les choix
      },
    },
  }),
  option: (provided, state) => ({
    ...provided,
    backgroundColor: 'rgb(37, 37, 37)',
    width: '100px', // Rduire la largeur du contrle
    minHeight: '30px', // Rduire la hauteur du contrle
  }),
  singleValue: (provided, state) => ({
    ...provided,
    color: 'white',
  }),
};

const options = [
  { value: 'fr', label: 'FR' },
  { value: 'en', label: 'EN' },
  { value: 'it', label: 'IT' },
];

function SelectLang() {
  const [value, setValue] = React.useState(options[0]); // Dfinir la valeur par dfaut sur la premire option (FR)

  const handleChange = (selectedOption) => {
    i18n.changeLanguage(selectedOption.value);
    setValue(selectedOption);
  };

  return (
    <Select
      options={options}
      value={value}
      onChange={handleChange}
      styles={customStyles}
    isSearchable={false} // Dsactiver la recherche
      theme={(theme) => ({
        ...theme,
        colors: {
          ...theme.colors,
          primary: 'white',
          primary25: 'white',
        },
      })}
      >
        {options.map((option) => (
        <MenuItem key={option.value} value={option.value}>
          {option.label}
        </MenuItem>
      ))}
    </Select>
  );
}

export default SelectLang;
