import i18n from 'i18next';
import { initReactI18next } from 'react-i18next';
import LanguageDetector from 'i18next-browser-languagedetector';

import translationEN from './en.json'; // Fichier JSON de traduction pour l'anglais
import translationFR from './fr.json'; // Fichier JSON de traduction pour le franais
import translationIT from './it.json'; // Fichier JSON de traduction pour l'italien

i18n
  .use(LanguageDetector)
  .use(initReactI18next)
  .init({
    resources: {
      en: {
        translation: translationEN,
      },
      fr: {
        translation: translationFR,
      },
      it: {
        translation: translationIT,
      },
    },
    fallbackLng: 'en', // Langue par dfaut si la traduction n'est pas disponible
    lng: 'fr', // default language
    interpolation: {
      escapeValue: false, // Permet l'utilisation de balises HTML dans les traductions
    },
  });

export default i18n;
