import React from 'react';
import Box from '@mui/material/Box';
import { useTranslation } from 'react-i18next';
import List from '@mui/material/List';
import GitHubIcon from '@mui/icons-material/GitHub';
import CallIcon from '@mui/icons-material/Call';
import EmailIcon from '@mui/icons-material/Email';
import LinkedInIcon from '@mui/icons-material/LinkedIn';
import LocationOnIcon from '@mui/icons-material/LocationOn';

const Contact = () => {
  const { t } = useTranslation();
  // Calculer la largeur en soustrayant la taille du menu
  const contentWidth = `calc(100vw - 240px)`; // Remplacez 240px par la largeur relle de votre menu

  const icons = (
    <List sx={{display:"flex", flexWrap:"wrap", width: '200px', justifyContent: 'space-between'}}>
        <GitHubIcon />
        <CallIcon />
        <EmailIcon />
        <LinkedInIcon />
    </List>
  )

  return (
    <Box
      component="main"
      sx={{
        flexGrow: 1,
        p: 3,
        height: '100vh',
        overflow: 'auto',
        backgroundColor: '#000000',
        width: contentWidth,
        ml: '240px', // Ajoute un espace  gauche pour le menu
      }}
    >
        <h1>{t('CONTACT')}</h1>
        <Box sx={{marginLeft: '150px', marginTop: '150px', textAlign: 'left'}}>
        <h2><LocationOnIcon></LocationOnIcon> EMPLACEMENT</h2><br></br>
        <p>Valbonne Sophia-Antipolis <br></br>06560</p><br></br><br></br>
        <h2>
            Mes Informations
        </h2>
        <br></br>
        {icons}
        </Box>
    </Box>
  );
};

export default Contact;
