import React from 'react';
import ImageCarousel from './imageCarousel';
import diplome from'../img/diplome2.jpg';
import chambre from '../img/chambre.jpg';
import ariane from '../img/ariane.jpg';
import raymond from '../img/raymond.jpg';

const Images = () => {
  const images = [
    diplome,
    chambre,
    ariane,
    raymond,
    // Ajoutez autant d'URL d'images que vous le souhaitez
  ];

  return (
    <div>
      <ImageCarousel images={images} />
    </div>
  );
};

export default Images;