import * as React from 'react';
import PropTypes from 'prop-types';
import AppBar from '@mui/material/AppBar';
import Box from '@mui/material/Box';
import CssBaseline from '@mui/material/CssBaseline';
import Drawer from '@mui/material/Drawer';
import List from '@mui/material/List';
import ListItem from '@mui/material/ListItem';
import ListItemButton from '@mui/material/ListItemButton';
import ListItemIcon from '@mui/material/ListItemIcon';
import ListItemText from '@mui/material/ListItemText';
import { createTheme } from '@mui/material/styles';
import Typography from '@mui/material/Typography';
import { grey } from '@mui/material/colors';
import ContactMailIcon from '@mui/icons-material/ContactMail';
//import CodeIcon from '@mui/icons-material/Code';
import DesktopWindowsSharpIcon from '@mui/icons-material/DesktopWindowsSharp';
import { Link } from 'react-router-dom';
import './drawer.css';
import SelectLang from './selectLang';
import { useTranslation } from 'react-i18next';
import SchoolIcon from '@mui/icons-material/School';
import HomeIcon from '@mui/icons-material/Home';
import SwitchBut from './mode';
import AccountCircleIcon from '@mui/icons-material/AccountCircle';
import Avatar from './avatar';

const theme = createTheme({
    palette: {
      primary: {
        main: '#000000', // Changer la couleur principale en bleu
      },
      text: {
        primary: '#ffff', // Changer la couleur du texte en blanc
      },
    },
  });

const drawerWidth = 240;

function ResponsiveDrawer(props) {
  const { t } = useTranslation();
  const { window } = props;
  const [mobileOpen, setMobileOpen] = React.useState(false);

  const handleDrawerToggle = () => {
    setMobileOpen(!mobileOpen);
  };

  const drawer = (
    <div sx={{ display: 'flex', flexDirection: 'column',  height: '100%',}} className="drawer-container">
      <List
        sx={{
          color: theme.palette.text.primary,
          display: 'flex',
          justifyContent: 'space-between',
        }}
      >
        <SwitchBut />
        <div></div> {/* Utilis pour crer un espace vide  gauche */}
        <SelectLang /> {/* Dplacez le composant SelectLang ici */}
      </List>
      <List
      sx={{
        marginBottom: '15vh', //marginBottom: '94%',
        color : theme.palette.text.primary
        
      }}>
        <Avatar />
        {/* <SelectLang sx={{marginLeft: '200px'}} > </SelectLang> */}
        <Typography variant="h4" gutterBottom sx={{textAlign: 'left', marginLeft: '20px', marginTop: '20px', marginBottom:'20px', fontFamily: 'inherit'}}>
        Louise Sanchez
      </Typography>
      
      {[
    { text: t('ACCUEIL'), icon: <HomeIcon sx={{ color: grey[500] }} />, path: '/' },
    { text: t('PROJETS'), icon: </*CodeIcon*/ DesktopWindowsSharpIcon sx={{ color: grey[500] }} />, path: '/mesProjets' },
    { text: t('FOMATION'), icon: <SchoolIcon sx={{ color: grey[500] }} />, path: '/formation' },
    { text: t('A_PROPOS'), icon: <AccountCircleIcon sx={{ color: grey[500] }} />, path: '/apropos' },

  // Autres lments de menu
    ].map((item) => (
    <ListItem key={item.text} disablePadding sx={{ marginBottom: '20px' }}>
        <ListItemButton component={Link} to={item.path}>
        <ListItemIcon>{item.icon}</ListItemIcon>
        <ListItemText primary={item.text} />
        </ListItemButton>
    </ListItem>
    ))}
      </List>
      <List
      sx={{
        color : theme.palette.text.primary,
      }}>
        {[{ text: t('CONTACT'), icon: <ContactMailIcon sx={{ color: grey[500] }} />, path: '/contact' }].map((item) => (
          <ListItem key={item.text} disablePadding>
          <ListItemButton component={Link} to={item.path}>
          <ListItemIcon>{item.icon}</ListItemIcon>
          <ListItemText primary={item.text} />
          </ListItemButton>
      </ListItem>
        ))}
      </List>
    </div>
  );

  const container = window !== undefined ? () => window().document.body : undefined;

  return (
    <Box sx={{ display: 'flex', flexDirection: 'column',  height: '100%',}} className="drawer-container">
      <CssBaseline />
      <AppBar
        position="fixed"
        sx={{
          width: { sm: `calc(100% - ${drawerWidth}px)` }, 
        }}
      >
      </AppBar>
      <Box 
        component="nav"
        sx={{
            width: { sm: drawerWidth },
            flexShrink: { sm: 0 },
          }}
        aria-label="mailbox folders"
      >
        {/* The implementation can be swapped with js to avoid SEO duplication of links. */}
        <Drawer
          container={container}
          variant="temporary"
          open={mobileOpen}
          onClose={handleDrawerToggle}
          ModalProps={{
            keepMounted: true, // Better open performance on mobile.
          }}
          sx={{
            display: { xs: 'block', sm: 'none' },
            '& .MuiDrawer-paper': { boxSizing: 'border-box', width: drawerWidth},          }}
        >
          {drawer}
        </Drawer>
        <Drawer
          variant="permanent"
          sx={{
            display: { xs: 'none', sm: 'block' },
            '& .MuiDrawer-paper': { boxSizing: 'border-box', width: drawerWidth},
          }}
          open
        >
          {drawer}
        </Drawer>
      </Box>
    </Box>
  );
}

ResponsiveDrawer.propTypes = {
  /**
   * Injected by the documentation to work in an iframe.
   * You won't need it on your project.
   */
  window: PropTypes.func,
};

export default ResponsiveDrawer;
