import React from 'react';
import Box from '@mui/material/Box';
import MediaCard from './card';

function MesProjets () {

  // Calculer la largeur en soustrayant la taille du menu
  const contentWidth = `calc(100vw - 240px)`; // Remplacez 240px par la largeur relle de votre menu

  return (
    <Box
      component="main"
      sx={{
        flexGrow: 1,
        p: 3,
        height: '100vh',
        overflow: 'auto',
        backgroundColor: '#000000',
        width: contentWidth,
        ml: '240px', // Ajoute un espace  gauche pour le menu
      }}
    >
        <h1>MES PROJETS</h1>
        <br></br>
        <MediaCard></MediaCard>
    </Box>
  );
};

export default MesProjets;
